.contact-form {
	width: 100%;
	height: 100%;
	overflow-y: scroll;
	overflow-x: hidden;
	/* display: none !important; */
	/* HOLA SACA ESTO Y SE VE EL FORM  */
}

.contact-form-container {
	width: 100%;
	height: 100%;
}

.contact-form-heading h2 {
	font-size: 20px;
	font-weight: 600;
	margin-top: 10px;
	text-transform: uppercase;
	color: #000;
}

.contact-form-heading p {
	color: rgb(39, 38, 38);
}

.contact-form-container {
	width: 100%;
	height: 100%;
	margin-top: 20px;
}

.contact-form-container input {
	outline: none;
	padding: 11px 12px;
	font-size: 14px;
	color: #000;
	margin-left: 10px;
	margin-bottom: 10px;
	width: 150px;
	height: 40px;
	letter-spacing: 1px;
	background: transparent;
	border: 1px solid #b1b1b1;
}

.contact-form-container .phone {
	width: 210px;
}

.contact-form-container .email {
	width: 370px;
}

.contact-form-container .empresa {
	width: 370px;
}

.contact-form-container .send {
	width: 150px;
	height: 40px;
	border-radius: 50px;
	margin-left: 35%;
	margin-top: 10px;
	color: #fff;
	border: solid 1px #e00f08;
	background-color: #ce110a;
	-webkit-box-shadow: 10px 10px 10px -6px rgba(166, 166, 166, 1);
	-moz-box-shadow: 10px 10px 10px -6px rgba(166, 166, 166, 1);
	box-shadow: 10px 10px 10px -6px rgba(166, 166, 166, 1);
}

