	background-image: linear-gradient(to left, #ce110a, #e6332d);
}

.pulse {
	display: table;
	width: 100px;
	height: 100px;
	background: #ce110a;
	cursor: pointer;
	box-shadow: 0 0 0 #ce110a;
	-webkit-animation: pulse 2s infinite;
	animation: pulse 2s infinite;
	text-align: center;
	border-radius: 50%;
}

.pulse i {
	font-size: 30px !important;
	display: table-cell;
	vertical-align: middle;
}

.play-icon {
	margin-top: 20px;
	margin-left: 5px;
}

@-webkit-keyframes pulse {
	0% {
		-webkit-box-shadow: 0 0 0 0 #ce110a;
	}
	70% {
		-webkit-box-shadow: 0 0 0 30px rgba(121, 142, 234, 0);
	}
	100% {
		-webkit-box-shadow: 0 0 0 0 rgba(121, 142, 234, 0);
	}
}

@keyframes pulse {
